import emIndex from '../view/testIndex'
import TestInput from '../view/testInput'
import TestSlide from '../view/testSlide'
import backtoTop from '../view/testbacktoTop'
import emLoading from '../view/testLoading'
import emDialog from '../view/testemDialog'
import msgBox from '../view/testemmsgBox'
import emButton from '../view/testemButton'
import emSwitch from '../view/testSwitch'
import emTab from '../view/testTab'
import emSteps from '../view/testSteps'
import emRadio from '../view/testRadio'
import emTips from '../view/testemTips'
import emTable from '../view/testTable'
import emCollapse from '../view/testCollapse'
import emRollnotice from '../view/testRollnotice'
import emSelect from '../view/testSelect'
import emCheckbox from '../view/testCheckbox'
import emCount from '../view/testCount'
import emProgress from '../view/testProgress'
import emBreadcrumb from '../view/testBreadcrumb'
import emDatepicker from '../view/testemDatepicker'
import emareaSelect from '../view/testemareaSelect'
import emlinkProvice from '../view/testemlinkProvice'
import emTool from '../view/testTool'

import filter from '../view/testFilter'
export default [
  {
    path: '/',
    name:'index',
    component: emIndex
  },
  {
    path: '/filter',
    name: 'filter',
    component: filter
  },
  {
    path: '/tool',
    name: 'tool',
    component: emTool
  },
  {
    path: '/input',
    name: 'TestInput',
    component: TestInput
  },
  {
    path: '/switch',
    name: 'emSwitch',
    component: emSwitch
  },
  {
    path: '/radio',
    name: 'emRadio',
    component: emRadio
  },
  {
    path: '/select',
    name: 'emSelect',
    component: emSelect
  },
  {
    path: '/checkbox',
    name: 'emCheckbox',
    component: emCheckbox
  },
  {
    path: '/count',
    name: 'emCount',
    component: emCount
  },
  {
    path: '/datepicker',
    name: 'emDatepicker',
    component: emDatepicker
  },
  {
    path: '/areaselect',
    name: 'emareaSelect',
    component: emareaSelect
  },
  {
    path: '/linkprovice',
    name: 'emlinkProvice',
    component: emlinkProvice
  },
  {
    path: '/progress',
    name: 'emProgress',
    component: emProgress
  },
  {
    path: '/table',
    name: 'emTable',
    component: emTable
  },
  {
    path: '/slide',
    name: 'TestSlide',
    component: TestSlide
  },

  {
    path: '/msgbox',
    name: 'msgBox',
    component: msgBox
  },
  {
    path: '/dialog',
    name: 'emDialog',
    component: emDialog
  },
  {
    path: '/button',
    name: 'emButton',
    component: emButton
  },

  {
    path: '/tab',
    name: 'emTab',
    component: emTab
  },
  {
    path: '/steps',
    name: 'emSteps',
    component: emSteps
  },
  {
    path: '/notice',
    name: 'emRollnotice',
    component: emRollnotice
  },
  {
    path: '/tips',
    name: 'emTips',
    component: emTips
  },

  {
    path: '/collapse',
    name: 'emCollapse',
    component: emCollapse
  },
  {
    path: '/breadcrumb',
    name: 'emBreadcrumb',
    component: emBreadcrumb
  },
  {
    path: '/backtoTop',
    name: 'backtoTop',
    component: backtoTop
  },
  {
    path: '/loading',
    name: 'emLoading',
    component: emLoading
  },
]



