<template>
  <div>
    <em-select ref="proviceTab" placeholder="请选择省份" :option="proviceData" @click="selectProvice"></em-select>
    <em-select ref="cityTab" placeholder="请选择城市" :option="cityData" @click="selectCity"></em-select>
    <em-select ref="districtTab" placeholder="请选择区域" :option="districtData" @click="selectDistrict"></em-select>
	</div>
</template>
<script>
export default {
  name: 'emLinkProvice',
  data () {
    return {
      tempCityData:[],
      proviceData:[],
      cityData:[],
      districtData:[]
    }
  },
  beforeCreate: function () {
    var that=this;
    this.$http.get('@/../static/cityarea.json').then(response => {
      that.allselectData=response.data.allProvice;
      that.allselectData.forEach(function(item){
        that.proviceData.push({
          value:item.name,
          label:item.name
        });
      })
    })
  },
  methods: {
    selectProvice(label){
      var that=this;
      console.log("省份",label);
      this.$refs.cityTab.chosenOption('请选择城市');
      this.$refs.districtTab.chosenOption('请选择区域');
      that.cityData=[];
      that.districtData=[];
      for (var item of that.allselectData) {
        if(item.name==label){
          that.tempCityData=item.city;
          item.city.forEach(function(cityname){
            that.cityData.push({
              value:cityname.name,
              label:cityname.name
            });
          })
        }
      }
    },
    selectCity(label){
      var that=this;
      console.log("城市",label);
      this.$refs.districtTab.chosenOption('请选择区域');
      for (var district of that.tempCityData) {
        if(district.name==label){
          that.districtData=[];
          district.districtAndCounty.forEach(function(districtname){
            that.districtData.push({
              value:districtname,
              label:districtname
            });
          })
        }
      }
    },
    selectDistrict(label){
      console.log("区域",label);
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../styles/linkprovice';
</style>



