<template>
<div class="datetime-picker">
  <div v-if="type=='date'" :style="{ width: width }">
       <input type="text" :class="show?'input-border':''" :readonly="readonly" placeholder="选择日期" :value="value" @click="show = !show">
       <i class="iconfont iconfont-time1"></i>
       <div class="picker-wrap" v-show="show">
           <table class="date-picker">
               <thead>
                   <tr class="date-head">
                     <th colspan="7">
                       <i class="iconfont iconfont-backward" @click="yearClick(-1)"></i>
                       <i class="iconfont iconfont-menu-left" @click="monthClick(-1)"></i>
                       <span class="show-yearmonth">{{curYear}}年 {{curMonth}}月</span>
                       <i class="iconfont iconfont-menu-right" @click="monthClick(1)"></i>
                       <i class="iconfont iconfont-forward" @click="yearClick(1)"></i>
                     </th>
                   </tr>
                   <tr class="date-days">
                       <th v-for="weekname in weekList">{{weekname}}</th>
                   </tr>
               </thead>
               <tbody>
                   <tr v-for="i in 6">
                       <td v-for="(item, index) in cutDatelist(i,datelist,7)" :class="item.classStr" @click="selectDate(item.year,item.month,item.showDate)">{{item.showDate}}</td>
                   </tr>
               </tbody>
           </table>
       </div>
   </div>
   <div v-if="type=='month'" class="month-listbox">
       <input type="text" :class="show?'input-border':''" :readonly="readonly" placeholder="选择月份" :value="value" @click="show = !show">
       <i class="iconfont iconfont-time1"></i>
       <div class="picker-wrap" v-show="show">
           <table class="date-picker">
               <thead>
                   <tr class="date-head">
                     <th colspan="7">
                       <i class="iconfont iconfont-backward" @click="selmonthClick(-1)"></i>
                       <span class="show-yearmonth">{{curYear}}年</span>
                       <i class="iconfont iconfont-forward" @click="selmonthClick(1)"></i>
                     </th>
                   </tr>
               </thead>
               <tbody>
                   <tr v-for="i in 3">
                       <td v-for="(item, index) in cutDatelist(i,monthList,4)" :class="datemonthChosen(i,index,value)?'value-chosen':''" @click="selectMonth(i,index)">{{item}}</td>
                   </tr>
               </tbody>
           </table>
       </div>
   </div>
   <div v-if="type=='year'" class="month-listbox">
       <input type="text" :class="show?'input-border':''" :readonly="readonly" placeholder="选择年份" :value="value" @click="show = !show">
       <i class="iconfont iconfont-time1"></i>
       <div class="picker-wrap" v-show="show">
           <table class="date-picker">
               <thead>
                   <tr class="date-head">
                     <th colspan="7">
                       <i class="iconfont iconfont-backward" @click="selyearClick(-1)"></i>
                       <span class="show-yearmonth">{{startNum}}年~{{endNum}}年</span>
                       <i class="iconfont iconfont-forward" @click="selyearClick(1)"></i>
                     </th>
                   </tr>
               </thead>
               <tbody>
                   <tr v-for="i in 3">
                       <td v-for="(item, index) in cutDatelist(i,yearlist,4)" :class="item==value?'value-chosen':''"  @click="selectYear(item)">{{item}}</td>
                   </tr>
               </tbody>
           </table>
       </div>
   </div>
</div>
</template>
<script>
const util = require('../utils/tool.js')
// 现在的时间
const todayDate = new Date(),
    todayYear = todayDate.getFullYear(),
    todayMonth = todayDate.getMonth() + 1,
    todayDay=todayDate.getDate();
      var nowdateYear=todayDate.getFullYear();
export default {
  name: 'em-datepicker',
  props: {
    format:{
      type:String,
      default:'-'
    },
    type:{
      type:String,
      default:'date'
    },
    readonly:{
      type:Boolean,
      default:false
    },
    ismultiple:{
      type:String,
      default:'single'  //multiple
    },
    width:{
      type:String,
      default:'300px'
    },
    disableback:{
      type:Boolean,
      default:false
    },
    disableforward:{
      type:Boolean,
      default:false
    }
  },
  data () {
      return {
          show: false,
          weekList: ['一','二','三','四','五','六','日'],
          monthList: ['一月','二月','三月','四月','五月','六月','七月','八月','九月','十月','十一月','十二月'],
          datelist:[],
          curYear:new Date().getFullYear(),
          curMonth:new Date().getMonth() + 1,
          value:'',
          formatLine:this.format,
          multipleData:[],
          startNum:2010,
          endNum:2020,
          yearlist:[]
      }
  },
  watch : {
    multipleData (val) {
      this.value=val.join(",");
    }
  },
  mounted(){
    this.init(this.curYear,this.curMonth);
    if(this.type=='year'){
      this.yearDatainit(nowdateYear);
    }
  },
  methods:{
    //增加一个方法，根据序号遍历，每次遍历返回7条数据，依次叠加（vue2去掉了过滤器的功能）
    cutDatelist(i,arr,row){
      return arr.slice((i-1)*row,i*row);
    },
    datemonthChosen(i,index,value){
      let selmonth=parseInt(value.split(this.formatLine)[1]);
      // console.log(selmonth,selmonth%4,parseInt(selmonth/4));
      return (index==selmonth%4-1)&&(i==parseInt(selmonth/4)+1);
    },
    //初始化日历
    init(year,month){
      var firstDay = new Date(year, month - 1, 1),
          firstDayWeekDay = firstDay.getDay();
      if (firstDayWeekDay === 0) {
          firstDayWeekDay = 7;
      }
      year = firstDay.getFullYear();
      month = firstDay.getMonth() + 1;
      let day = firstDay.getDate(),
          lastDayOfLastMonth = new Date(year, month - 1, 0),
          lastDateOfLastMonth = lastDayOfLastMonth.getDate(),
          preMonthDayCount = firstDayWeekDay - 1,
          lastDay = new Date(year, month, 0),
          lastDate = lastDay.getDate();
      this.datelist=[];
      for (let i = 0; i < 7 * 6; i++) {
          var date = i + 1 - preMonthDayCount;
          var showDate = date;
          var thisMonth = month;
          var thisYear = year;
          if (date <= 0) {
              thisMonth = month - 1;
              showDate = lastDateOfLastMonth + date;
          } else if (date > lastDate) {
              thisMonth = month + 1;
              showDate = showDate - lastDate;
          }
          if (thisMonth == 0) {
              thisMonth = 12;
              thisYear+=1;
          }
          if (thisMonth == 13) {
              thisMonth = 1;
              thisYear+=1;
          }
          this.datelist.push({
              year:thisYear,
              month: thisMonth,
              date: date,
              showDate: showDate,
              classStr:''
          });
      }
      for (let [index, elem] of this.datelist.entries()) {
        //上一个月和下一个在本月显示的天数 添加灰色样式
        if(index<=preMonthDayCount-1 || index>(lastDate+preMonthDayCount-1)){
          this.datelist[index].classStr="prenext-date"
        }
        if((todayYear>this.datelist[index].year)||(todayYear==this.datelist[index].year&&this.datelist[index].month<todayMonth)){
          this.datelist[index].classStr+=" date-disabled"
        }
        if((todayYear==this.datelist[index].year)&&(todayMonth==this.datelist[index].month)&&(todayDay==this.datelist[index].date)){
          this.datelist[index].classStr+=" date-now"
        }
        if(this.disableback){
          if(((todayYear==this.datelist[index].year)&&((this.datelist[index].month<todayMonth)||((this.datelist[index].month==todayMonth)&&(todayDay>this.datelist[index].date)))||(todayYear>this.datelist[index].year))){
            this.datelist[index].classStr+=" date-disabled"
          }
          else{
            this.datelist[index].classStr=this.datelist[index].classStr.replace("date-disabled","");
          }
        }
        if(this.disableforward){
          if(((todayYear==this.datelist[index].year)&&((this.datelist[index].month>todayMonth)||((this.datelist[index].month==todayMonth)&&(todayDay<this.datelist[index].date)))||(todayYear<this.datelist[index].year))){
            this.datelist[index].classStr+=" date-disabled"
          }
          else{
            this.datelist[index].classStr=this.datelist[index].classStr.replace("date-disabled","");
          }
        }
        //如果日历在年份和月份切换的时候，重新比较日期，添加已选的样式
        if(this.ismultiple=='multiple'){
          for (let [index, elem] of this.datelist.entries()) {
            var seldate=[this.datelist[index].year,util.numLessTen(this.datelist[index].month),util.numLessTen(this.datelist[index].showDate)].join(this.formatLine);
            if(this.multipleData.includes(seldate)){
              this.datelist[index].classStr+=" date-active"
            }
          }
        }
        else{
          let nowdatetime=[this.datelist[index].year,util.numLessTen(this.datelist[index].month),util.numLessTen(this.datelist[index].showDate)].join(this.formatLine);
          if((nowdatetime==this.value)&&(!this.datelist[index].classStr.includes("date-active"))){
            this.datelist[index].classStr+=" date-active"
          }
        }
      }
      //对过去的时间 添加not-allowed样式
      if(this.disableback){
        const $yearback=$(this.$el).find(".iconfont-backward");
        const $monthback=$(this.$el).find(".iconfont-menu-left");
        if((this.curYear<=todayYear)||((this.curYear-1==todayYear)&&(this.curMonth<todayMonth))){
          $yearback.addClass("iconfont-notallowed");
        }
        if((this.curYear<todayYear)||((this.curYear==todayYear)&&(this.curMonth<=todayMonth))){
          $yearback.addClass("iconfont-notallowed");
        }
        if((this.curYear-todayYear>1)){
          if($yearback.hasClass("iconfont-notallowed")){
            $yearback.removeClass("iconfont-notallowed");
          }
        }
        if((this.curYear==todayYear)&&(this.curMonth<=todayMonth)){
          $monthback.addClass("iconfont-notallowed");
        }
        else{
          $monthback.removeClass("iconfont-notallowed");
        }
      }
      //对将来的时间 添加not-allowed样式
      if(this.disableforward){
        const $yearforward=$(this.$el).find(".iconfont-forward");
        const $monthforward=$(this.$el).find(".iconfont-menu-right");
        if((this.curYear==todayYear)){
          $yearforward.addClass("iconfont-notallowed");
          if(this.curMonth>=todayMonth){
            $monthforward.addClass("iconfont-notallowed");
          }
          else{
            $monthforward.removeClass("iconfont-notallowed");
          }
        }
        if((todayYear-1==this.curYear)&&(this.curMonth>todayMonth)){
          $yearforward.addClass("iconfont-notallowed");
          $monthforward.removeClass("iconfont-notallowed");
        }
        if(todayYear-1>=this.curYear){
          $yearforward.removeClass("iconfont-notallowed");
        }
      }
    },
    selectMonth(i,month){
      this.value=[this.curYear,util.numLessTen((i-1)*4+month+1)].join(this.formatLine);
      this.show=false;
    },
    selectYear(year){
      this.value=year;
      this.show=false;
    },
    selectDate(year,month,day){
      //多选时间
      if(this.ismultiple=='multiple'){
        let tempValue=[year,util.numLessTen(month),util.numLessTen(day)].join(this.formatLine);
        for(var j in this.multipleData){
          //多选数据集合里面如果有数据，就删除掉
          if(this.multipleData[j]==tempValue){
              this.multipleData.splice(j,1);
              //删除样式
              for (let [index, elem] of this.datelist.entries()) {
                var deldate=[this.datelist[index].year,util.numLessTen(this.datelist[index].month),util.numLessTen(this.datelist[index].showDate)].join(this.formatLine);
                if(deldate==tempValue){
                  this.datelist[index].classStr=this.datelist[index].classStr.replace("date-active","");
                  return ;
                }
              }
              return ;
          }
        }
        this.multipleData.push(tempValue);
        //遍历数据，给已经选好的数据添加样式
        for (let [index, elem] of this.datelist.entries()) {
          let tempdate=[this.datelist[index].year,util.numLessTen(this.datelist[index].month),util.numLessTen(this.datelist[index].showDate)].join(this.formatLine);
          if(this.multipleData.includes(tempdate)&&(!this.datelist[index].classStr.includes("date-active"))){
            this.datelist[index].classStr+=' date-active';
          }
        }
      }
      //单选时间
      else{
        if(this.disableback){
          if(((todayYear==year)&&((month<todayMonth)||((month==todayMonth)&&(todayDay>day)))||(todayYear>year))){
            return ;
          }
        }
        if(this.disableforward){
          if(((todayYear==year)&&((month>todayMonth)||((month==todayMonth)&&(todayDay<day)))||(todayYear<year))){
            return ;
          }
        }
        this.value=[year,util.numLessTen(month),util.numLessTen(day)].join(this.formatLine);
        for (let [index, elem] of this.datelist.entries()) {
          var nowdatetime=[this.datelist[index].year,util.numLessTen(this.datelist[index].month),util.numLessTen(this.datelist[index].showDate)].join(this.formatLine);
          if(((this.datelist[index].showDate==day)&&(this.datelist[index].month==month)&&(this.datelist[index].year==year))||(this.value==nowdatetime)){
            if(!this.datelist[index].classStr.includes("date-active")){
              this.datelist[index].classStr+=' date-active';
            }
          }
          else{
            this.datelist[index].classStr=this.datelist[index].classStr.replace("date-active","");
          }
        }
        this.show=false;
      }
    },
    yearClick(flag){
      const $yearback=$(this.$el).find(".iconfont-backward");
      const $yearforward=$(this.$el).find(".iconfont-forward");
      if(this.disableback){
        if(flag==-1){
          if((this.curYear<=todayYear)||((this.curYear-todayYear==1)&&(this.curMonth<todayMonth))){
            $yearback.addClass("iconfont-notallowed");
            return ;
          }
        }
      }
      if(this.disableforward){
        if(flag==1){
          if((this.curYear>=todayYear)||((todayYear-this.curYear==1)&&(this.curMonth>todayMonth))){
            $yearforward.addClass("iconfont-notallowed");
            return ;
          }
        }
      }
      this.curYear+=flag;
      this.init(this.curYear,this.curMonth);
    },
    monthClick(flag){
      if(this.disableback){
        if((this.curYear==todayYear)&&(this.curMonth<=todayMonth)&&(flag==-1)){
          return ;
        }
      }
      if(this.disableforward){
        if((this.curYear==todayYear)&&(this.curMonth>=todayMonth)&&(flag==1)){
          return ;
        }
      }
      this.curMonth+=flag;
      if(this.curMonth==0){
        this.curMonth=12;
        this.curYear-=1;
      }
      if(this.curMonth==13){
        this.curMonth=1;
        this.curYear+=1;
      }
      this.init(this.curYear,this.curMonth);
    },
    selmonthClick(flag){
      this.curYear+=flag;
    },
    selyearClick(flag){
      nowdateYear+=flag*10;
      this.startNum+=flag*10;
      this.endNum+=flag*10;
      this.yearlist=[];
      this.yearDatainit(nowdateYear);
    },
    yearDatainit(nowdateYear){
      let lastnum=nowdateYear.toString().slice(-1);
      this.startNum=nowdateYear-lastnum;
      this.endNum=nowdateYear+(9-lastnum);
      for (let i = this.startNum; i <= this.endNum; i++) {
        this.yearlist.push(i);
      }
    }

  }
}
</script>
<style lang="scss" scoped>
.datetime-picker {
    position: relative;
    display: inline-block;
    color: #333;
    box-sizing: border-box;
    input {
        width: 65%;
        padding: 5px 10px 5px 35px;
        height: 30px;
        outline: 0 none;
        border: 1px solid #ccc;
        font-size: 13px;
        transition: border-color .2s cubic-bezier(.645,.045,.355,1);
        &.input-border{
          border-color:#409eff;
        }
    }
    i.iconfont-time1{
      position: absolute;
      left:10px;
      top:10px;
      font-size: 18px;
      color:#ccc;
    }
    .picker-wrap {
        position: absolute;
        z-index: 1000;
        width: 250px;
        height: auto;
        margin-top: 2px;
        box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
        border: 1px solid #dfe4ed;
        background: #fff;
        border-radius: 4px;
    }
    table {
        width: 100%;
        border-collapse: collapse;
        border-spacing: 0;
        text-align: center;
        font-size: 13px;
        tr {
            height: 34px;
            border: 0 none;
            margin:5px;
            &.date-head {
                text-align: center;
                color: #fff;
                font-size: 13px;
            }
            &.date-days {
              color: #5a5e66;
              font-weight: 400;
              font-size: 14px;
              border-bottom: 1px solid #e6ebf5;
            }
        }
        th,td{
          user-select: none;
          width: 34px;
          height: 34px;
          padding: 0;
          border: 0 none;
          line-height: 34px;
          text-align: center;
          border-radius: 50%;
        }
        th{
            .iconfont-backward,.iconfont-menu-left,.iconfont-menu-right,.iconfont-forward{
              margin-left:10px;
              font-size: 12px;
              font-style: normal;
              color: #2d2f33;
              border: 0;
              background: transparent;
              cursor: pointer;
              outline: none;
            }
            .iconfont-notallowed{
              cursor: not-allowed;
            }
            .show-yearmonth {
                display: inline-block;
                min-width: 120px;
                cursor: pointer;
                color: #5a5e66;
            }
        }
        td {
            cursor: pointer;
            position: relative;
            &:hover{
              background-color: #f0f0f0;
            }
            &.value-chosen{
              color: #409eff;
            }
            &.date-pass,&.date-future{
              color: #b4bccc;
            }
            &.date-active {
              color: #fff;
              background-color: #409eff;
              border-radius: 50%;
            }
            &.date-now{
              color: #409eff;
              &:after{
                content: '';
                position: absolute;
                bottom: 2px;
                left:0;
                right: 0;
                margin:0 auto;
                width: 5px;
                height: 5px;
                border-radius: 50%;
                background-color: #409eff;
              }
            }
            &.date-disabled{
              cursor: not-allowed;
            }
        }
    }
    .month-listbox{
      tr{
        td{
          height: 50px;
          line-height: 50px;
          &:hover{
            background-color: transparent;
            color: #409eff;
          }
        }
      }
    }
}
.prenext-date{
  color: #b4bccc;
}
</style>



