<template>
  <div>
		<input
    :class="[disabled?'disabled-class':'',focused?'input-border':'']"
    class="input-form"
		:placeholder="placeholder"
    :disabled="disabled"
		type="text"
    @focus="focused=true"
    @blur="focused=false"
    @input="$emit('input', $event.target.value)"
		/>
	</div>
</template>
<script>
export default {
  name: 'eminput',
  data () {
    return {
      focused:false
    }
  },
  props: {
  	placeholder: {
      type:String,
      default:'请输入内容'
    },
    disabled : {
      type:Boolean,
      default:false
    }
  },
  methods: {

  }
}
</script>
<style lang="scss" scoped>
.input-form{
  padding-left: 10px;
  outline:none;
  height: 35px;
  width: 200px;
  line-height: 35px;
  border:1px solid #D4D4D4;
  transition: border-color .2s cubic-bezier(.645,.045,.355,1);
  &.input-border{
    border-color:#409eff;
  }
}
.disabled-class{
  cursor: not-allowed;
  background-color: #f5f7fa;
  color: #b4bccc;
}
</style>



