<template>
  <div>
    <h2 class="title">面包屑</h2>
	<h3 class="sub">默认</h3>
	<em-breadcrumb :linkList="linkList"></em-breadcrumb>
	<pre>&lt;em-breadcrumb :linkList="linkList"&gt;&lt;/em-breadcrumb&gt;
&lt;script&gt;
export default {
  name: 'breadcrumb',
  data () {
    return {
      	linkList: [
			{
				name: '文本框',
				link: '/'
			},
			{
				name: '导航栏',
				link: '#/slide'
			},
			{
				name: '下拉框',
				link: '#/select'
			},
			{
				name: '面包屑',
				link: '#/breadcrumb'
			}
		]
    }
  }
}
&lt;/script&gt;
</pre>
	<h3 class="sub">自定义分割符</h3>
	<em-breadcrumb :linkList="linkList" separator=">"></em-breadcrumb>
	<pre>&lt;em-breadcrumb :linkList="linkList" separator=">"&gt;&lt;/em-breadcrumb&gt;
&lt;script&gt;
export default {
  name: 'breadcrumb',
  data () {
    return {
      	linkList: [
			{
				name: '文本框',
				link: '/'
			},
			{
				name: '导航栏',
				link: '#/slide'
			},
			{
				name: '下拉框',
				link: '#/select'
			},
			{
				name: '面包屑',
				link: '#/breadcrumb'
			}
		]
    }
  }
}
&lt;/script&gt;
</pre>
	<h3 class="sub">属性</h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th>属性名</th>
			<th>类型</th>
			<th>默认值</th>
			<th>描述</th>
		</tr>
		<tr>
			<td>linkList</td>
			<td>Array</td>
			<td>null</td>
			<td>面包屑数组</td>
		</tr>
		<tr>
			<td>separator</td>
			<td>String</td>
			<td>/</td>
			<td>分隔符</td>
		</tr>
	</table>
  </div>
</template>

<script>
export default {
  name: 'breadcrumb',
  data () {
    return {
      	linkList: [
			{
				name: '文本框',
				link: '/'
			},
			{
				name: '导航栏',
				link: '#/slide'
			},
			{
				name: '下拉框',
				link: '#/select'
			},
			{
				name: '面包屑',
				link: '#/breadcrumb'
			}
		]
    }
  },
  methods:{
	
  }
}
</script>
<style scoped>

</style>



