<template>
  <div class="em-datepicker">
  	<h2 class="title">选择时间插件</h2>
	<h3 class="sub">默认(单选日期)</h3>
    <em-datepicker></em-datepicker>
	<pre>&lt;em-datepicker&gt;&lt;/em-datepicker&gt;</pre>
    <h3 class="sub">多选日期</h3>
    <em-datepicker ismultiple="multiple"></em-datepicker>
	<pre>&lt;em-datepicker type="multiple"&gt;&lt;/em-datepicker&gt;</pre>
    <h3 class="sub">过去的时间不能选</h3>
    <em-datepicker :disableback="disableback"></em-datepicker>
	<pre>&lt;em-datepicker :disableback="disableback"&gt;&lt;/em-datepicker&gt;</pre>
	<h3 class="sub">属性</h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th>属性名</th>
			<th>类型</th>
			<th>默认值</th>
			<th>描述</th>
		</tr>
		<tr>
			<td>ismultiple</td>
			<td>String</td>
			<td>single</td>
			<td>多选日期</td>
		</tr>
		<tr>
			<td>disableback</td>
			<td>Boolean</td>
			<td>false</td>
			<td>不能选择过去的时间</td>
		</tr>
	</table>
  </div>
</template>
<script>
export default {
  name: 'datepicker',
  data () {
    return {
      disableback:true,
      disableforward:true
    }
  },
  methods:{

  }
}
</script>
<style scoped>

</style>



