<template>
	<div class="em-steps">
		<div class="em-step" :class="item.status" v-for="(item,i) in stepsItem">
			<div class="em-step-head">
				<div class="em-step-line"><i :class="item.lineIsBlue?'blue-line':''"></i></div>
				<div class="em-step-icon">
					<div>{{i+1}}</div>
				</div>
			</div>
			<div class="em-step-main">
				<div class="em-step-title">{{item.title}}</div>
				<div class="em-step-description">{{item.description}}</div>
			</div>
		</div>
	</div>
</template>
<script>
export default {
  name: 'steps',
  data () {
    return {

    }
  },
  props: {
  	stepsItem: {
		type: Array
	}
  },
  methods: {

  },
  computed: {

  }
}
</script>
<style lang="scss" scoped>
.em-steps{
	display: flex;
	white-space: nowrap;
	.em-step{
		position: relative;
		flex-shrink: 1;
		display: inline-block;
		flex-basis: 33.3333%;
		&.finish{
			.em-step-head{
				color: #409eff;
				border-color: #409eff;
			}
			.em-step-main{
				.em-step-title{
					color: #409eff;
				}
				.em-step-description{
					color: #409eff;
				}
			}
		}
		&.wait{
			.em-step-head{
				color: #b4bccc;
				border-color: #b4bccc;
			}
			.em-step-main{
				.em-step-title{
					color: #b4bccc;
				}
				.em-step-description{
					color: #b4bccc;
				}
			}
		}
		&:last-of-type{
			.em-step-line {
				display: none;
			}
		}
		.em-step-head{
			position: relative;
			width: 100%;
			text-align: center;
			.em-step-line{
				position: absolute;
				border-color: inherit;
				background-color: #b4bccc;
				height: 2px;
				top: 11px;
				left: 50%;
				right: -50%;
				i{
					display: block;
					border-width: 1px;
					border-style: solid;
					border-color: inherit;
					transition: .15s ease-out;
					box-sizing: border-box;
					height: 0;
					transition-delay: 0ms;
					border-width: 1px;
					width: 0;
					&.blue-line{width: 100%;}
				}
			}
			.em-step-icon{
				position: relative;
				z-index: 1;
				display: inline-flex;
				justify-content: center;
				align-items: center;
				width: 24px;
				height: 24px;
				font-size: 14px;
				box-sizing: border-box;
				background: #fff;
				transition: .15s ease-out;
				border-radius: 50%;
				border: 2px solid;
				border-color: inherit;
				div{
					display: inline-block;
					user-select: none;
					text-align: center;
					font-weight: 700;
					line-height: 1;
					color: inherit;
				}
			}
		}
		.em-step-main{
			text-align: center;
			white-space: normal;
			.em-step-title{
				font-size: 16px;
				line-height: 38px;
			}
			.em-step-description{
				padding: 0 20%;
				margin-top: -5px;
				font-size: 12px;
				line-height: 20px;
				font-weight: 400;
			}
		}
	}
}
</style>



