<template>
	<table border=0; cellspacing=0; cellpadding=0; class="em-table" :class="classStr" :label="label" :tableData="tableData" :stripe="stripe" :hover="hover">
		<thead>
			<tr>
				<th v-for="(item,index) in label">{{item.name}}</th>
			</tr>
		</thead>
		<tbody>
			<tr v-for="(item,index) in tableData">
				<td v-for="(labelItem,labelIndex) in label">{{item[labelItem.prop]}}</td>
			</tr>
		</tbody>
	</table>
</template>
<script>
export default {
  name: 'table',
  data () {
    return {
		
    }
  },
  props: {
  	label: {
		type: Array
	},
	tableData: {
		type: Array
	},
	stripe: {
		type: Boolean
	},
	hover: {
		type: Boolean
	}
  },
  methods: {
	
  },
  computed: {
  	classStr(){
		var className = [this.stripe?'stripe':'',this.hover?'hover':''];
		return className.join(' ');
	}
  }
}
</script>
<style lang="scss" scoped>
.em-table{
	font-size: 14px;
    color: #5a5e66;
	width: 100%;
	padding: 0 10px;
	thead {
		color: #878d99;
		font-weight: 500;
	}
	th,td{
		border-bottom: 1px solid #e6ebf5;
		padding: 10px 0;
		text-align: center;
	}
	&.stripe{
		tr:nth-of-type(even){
			background: #fafafa;
		}
	}
	&.hover{
		tr:hover{
			background: #f5f7fa;
		}
	}
}
</style>



