<template>
  <div>
  	<h2 class="title">进度条</h2>
    <em-progress percent="0%"></em-progress>
	<em-progress percent="70%"></em-progress>
	<em-progress percent="100%" status="success"></em-progress>
	<em-progress percent="50%" status="exception"></em-progress>
	<pre>&lt;em-progress percent="0%"&gt;&lt;/em-progress&gt;
&lt;em-progress percent="70%"&gt;&lt;/em-progress&gt;
&lt;em-progress percent="100%" status="success"&gt;&lt;/em-progress&gt;
&lt;em-progress percent="50%" status="exception"&gt;&lt;/em-progress&gt;
</pre>
	<h3 class="sub">属性</h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th>属性名</th>
			<th>类型</th>
			<th>默认值</th>
			<th>描述</th>
		</tr>
		<tr>
			<td>percent</td>
			<td>String</td>
			<td>null</td>
			<td>百分比</td>
		</tr>
		<tr>
			<td>status</td>
			<td>String</td>
			<td>null</td>
			<td>进度状态</td>
		</tr>
	</table>
  </div>
</template>

<script>
export default {
  name: 'progress',
  data () {
    return {
      	
    }
  },
  methods:{
	
  }
}
</script>
<style scoped>

</style>



