<template>
  <div>
      <button type="button" @click="btnClick($event)" name="button" :class="classStr">{{text}}</button>
	</div>
</template>
<script>
export default {
  name: 'embutton',
  data () {
    return {
      btntype:'default'
    }
  },
  props: {
    type:{
      type:String,
      default:'default'
    },
    text:{
      type:String,
      default:'按钮'
    },
    round:{
      type:Boolean,
      default:false
    },
    size:{
      type:String,
      default:'default'
    }
  },
  computed: {
    classStr() {
      var className=['base-class',this.typeclass];
      if(this.round){
        className.push('round-btn');
      }
      if(this.size){
        className.push(this.sizeclass);
      }
      return className.join(" ");
    },
    typeclass(){
      switch (this.type) {
        case 'primary':
          return 'primary-class';break;
        case 'success':
            return 'success-class';break;
        case 'warning':
            return 'warning-class';break;
        case 'danger':
            return 'danger-class';break;
        default: return "";
      }
    },
    sizeclass(){
      switch (this.size) {
        case 'default':
          return 'large-class';break;
        case 'middle':
            return 'middle-class';break;
        case 'small':
            return 'small-class';break;
        default: return "";
      }
    }
  },
  methods:{
    btnClick($event){
      this.$emit("click",$event);
    }
  }
}
</script>
<style lang="scss" scoped>
.base-class{
  display: inline-block;
  line-height: 1;
  white-space: nowrap;
  cursor: pointer;
  background: #fff;
  border: 1px solid #d8dce5;
  border-color: #d8dce5;
  color: #5a5e66;
  text-align: center;
  box-sizing: border-box;
  outline: none;
  margin: 0;
  transition: .1s;
  font-weight: 500;
  user-select: none;
  padding: 12px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.primary-class{
  color: #fff;
  background-color: #409eff;
  border-color: #409eff;
}
.success-class{
  color: #fff;
  background-color: #67c23a;
  border-color: #67c23a;
}
.warning-class{
  color: #fff;
  background-color: #eb9e05;
  border-color: #eb9e05;
}
.danger-class{
  color: #fff;
  background-color: #fa5555;
  border-color: #fa5555;
}
.round-btn{
  border-radius: 20px;
}
.middle-class{
  padding: 10px 20px;
  font-size: 14px;
  border-radius: 4px;
}
.small-class{
  padding: 9px 15px;
  font-size: 12px;
  border-radius: 3px;
}
</style>



