<template>
  <div>
    <h2 class="title">计数器</h2>
	<h3 class="sub">默认</h3>
	<em-count></em-count>
	<pre>&lt;em-count&gt;&lt;/em-count&gt;</pre>
  	<h3 class="sub">设定最小和最大值</h3>
	<em-count min="5" max="10"></em-count>
	<pre>&lt;em-count min="5" max="10"&gt;&lt;/em-count&gt;</pre>
	<h3 class="sub">禁用状态</h3>
	<em-count disabled></em-count>
	<pre>&lt;em-count disabled&gt;&lt;/em-count&gt;</pre>
	<h3 class="sub">自定义步长</h3>
	<em-count min="1" max="20" step="2"></em-count>
	<pre>&lt;em-count min="1" max="20" step="2"&gt;&lt;/em-count&gt;</pre>
	<h3 class="sub">属性</h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th>属性名</th>
			<th>类型</th>
			<th>默认值</th>
			<th>描述</th>
		</tr>
		<tr>
			<td>min</td>
			<td>String</td>
			<td>1</td>
			<td>最小值</td>
		</tr>
		<tr>
			<td>max</td>
			<td>String</td>
			<td>null</td>
			<td>最大值</td>
		</tr>
		<tr>
			<td>disabled</td>
			<td>Boolean</td>
			<td>false</td>
			<td>禁用计数器</td>
		</tr>
		<tr>
			<td>step</td>
			<td>String</td>
			<td>1</td>
			<td>步长</td>
		</tr>
	</table>
  </div>
</template>

<script>
export default {
  name: 'count',
  data () {
    return {
      	
    }
  },
  methods:{
	
  }
}
</script>
<style scoped>

</style>



