<template>
  <div class="emRollnotice" :class="className">
    <ul class="scroll-notice">
      <li v-for="(item, i) in rolllist">{{item}}</li>
    </ul>
  </div>
</template>
<script>
export default {
  name: 'notice',
  data () {
    return {

    }
  },
  props: {
    speed:{
      type:Number,
      default:4
    },
    className:{
      type:String
    },
    rolllist:{
      type:Array,
      default:()=>['滚动内容一','滚动内容二','滚动内容三','滚动内容四']
    },
    keepmove:{
      type:Boolean,
      default:false
    },
    step:{
      type:Number,
      // validator: function (value) {
      //   return value > 0 && value<150
      // },
      default:100
    },
    line:{
      type:Number,
      default:1
    },
    direction:{
      type:String,
      default:'vertical'
    }
  },
  mounted () {
    const that=this,
          $curRoll=$(that.$el),
          $ul = $curRoll.find("ul"),
          height = $ul.find("li:first").height();
    if(that.line&&that.line>1){
      $curRoll.css("height","60px");
    }
    if(that.direction&&that.direction=='horizontal'){
      $curRoll.find("li").css("display","inline-block");
      let timerHor=null;
      var translateXlen=0;
      function horizontalMove(){
        if (translateXlen == -$curRoll.find("ul").innerWidth()) {
            translateXlen = 0;
        }
        translateXlen -= 1;
        $curRoll.find("ul").css({
            "transform":`translateX(${translateXlen}px`
        })
      }
      timerHor = setInterval(horizontalMove, 20);


    }
    if(that.keepmove&&that.direction!='horizontal'){
      let keepmoveTimer=null;
      function autoplay(){
        keepmoveTimer = setInterval(function(){
    			if($curRoll.find("ul").height()<$curRoll.height()){
    				clearInterval(keepmoveTimer);
    			}else{
            $curRoll.find("ul").animate({
      				marginTop: '-=1'
      			},0,function(){
      				const mtop = Math.abs(parseInt($(this).css("margin-top")));
      				if(mtop >= height){
                $(this).find("li").slice(0, 1).appendTo($(this));
    				    $(this).css("margin-top", 0);
      				}
      			});
    			}
    		}, that.step);
      }
      autoplay();
  		$curRoll.hover(function(){
  			clearInterval(keepmoveTimer);
  		},function(){
        autoplay();
  		});
    }
    if(!that.keepmove&&that.direction!='horizontal'){
      let Timer=null;
      $curRoll.hover(function () {
        clearInterval(Timer);
      }, function () {
        Timer = setInterval(()=> {
          $ul.animate({ "marginTop": -height + "px" }, 1000, function () {
            $(this).css({marginTop : "0px"}).find("li:first").appendTo(this);
          });
        }, that.speed*1000);
      }).trigger("mouseleave");
    }
  }
}
</script>
<style scoped lang="scss">
.emRollnotice{
  width:auto;
  height:30px;
  overflow:hidden;
  border: 1px solid #AAAAAA;
  display: inline-block;
  padding:0 10px;
  .scroll-notice{
    li{
      height:30px;
      line-height: 30px;
      font-size:14px;
      padding:0 10px;
    }
  }
}
</style>



