<template>
  <div class="em-progress" :class="status" :percent="percent" :status="status">
  	<div class="em-progress-bar">
		<div class="em-progress-bar-outer">
			<div class="em-progress-bar-inner" :style="{width: percent}"></div>
		</div>
	</div>
	<div class="em-progress-text"><span v-show="percent!='100%'">{{percent}}</span><i v-show="percent=='100%'"></i></div>
  </div>
</template>
<script>
export default {
  name: 'progress',
  data () {
    return {
		
    }
  },
  props: {
  	percent: {
		type: String
	},
	status: {
		type: String
	}
  },
  methods: {
	
  },
  computed: {
	
  }
}
</script>
<style lang="scss" scoped>
.em-progress{
	position: relative;
    line-height: 1;
	margin-bottom: 15px;
    width: 350px;
	.em-progress-bar{
		padding-right: 50px;
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		margin-right: -55px;
		box-sizing: border-box;
		.em-progress-bar-outer{
			height: 6px;
			border-radius: 100px;
			background-color: #e6ebf5;
			overflow: hidden;
			position: relative;
			vertical-align: middle;
			.em-progress-bar-inner{
				position: absolute;
				left: 0;
				top: 0;
				height: 100%;
				background-color: #409eff;
				text-align: right;
				border-radius: 100px;
				line-height: 1;
				white-space: nowrap;
				&:after {
					display: inline-block;
					content: "";
					height: 100%;
					vertical-align: middle;
				}
			}
		}
	}
	.em-progress-text{
		font-size: 14px;
		color: #5a5e66;
		display: inline-block;
		vertical-align: middle;
		margin-left: 10px;
		line-height: 1;
	}
	&.success{
		.em-progress-bar .em-progress-bar-outer .em-progress-bar-inner {
			background-color: #67c23a;
		}
		.em-progress-text {
			color: #67c23a;
		}
		i{
			display: inline-block;
			position: relative;
			border-radius: 50% 50%;
			background-color: #67c23a;
			width: 12px;
			height: 12px;
			&:after{
				content: "";
				border: 1px solid #fff;
				border-left: 0;
				border-top: 0;
				position: absolute;
				top: 2px;
				left: 4px;
				width: 3px;
				height: 6px;
				-webkit-transform: rotate(45deg);
    			transform: rotate(45deg);
			}
		}
	}
	&.exception{
		.em-progress-bar .em-progress-bar-outer .em-progress-bar-inner {
			background-color: #fa5555;
		}
		.em-progress-text {
			color: #fa5555;
		}
	}
}
</style>



