<template>
  <div>
  	<h2 class="title">下拉框</h2>
  	<h3 class="sub">默认</h3>
  	<em-select :option="option"></em-select>
	<pre>&lt;em-select :option="option"&gt;&lt;/em-select&gt;
&lt;script&gt;
export default {
  name: 'select',
  data () {
    return {
      	option: [
			{
				value: '1',
				label: '选项一'
			},
			{
				value: '2',
				label: '选项二'
			},
			{
				value: '3',
				label: '选项三'
			},
			{
				value: '4',
				label: '选项四'
			},
			{
				value: '5',
				label: '选项五'
			}
		]
    }
  }
}
&lt;/script&gt;
</pre>
  	<h3 class="sub">禁用</h3>
    <em-select placeholder="禁止选择" disabled></em-select>
	<pre>&lt;em-select placeholder="禁止选择" disabled&gt;&lt;/em-select&gt;</pre>
	<h3 class="sub">属性</h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th>属性名</th>
			<th>类型</th>
			<th>默认值</th>
			<th>描述</th>
		</tr>
		<tr>
			<td>option</td>
			<td>Array</td>
			<td>null</td>
			<td>选项</td>
		</tr>
		<tr>
			<td>placeholder</td>
			<td>String</td>
			<td>请输入内容</td>
			<td>提示语</td>
		</tr>
		<tr>
			<td>disabled</td>
			<td>Boolean</td>
			<td>false</td>
			<td>禁用下拉框</td>
		</tr>
	</table>
  </div>
</template>

<script>
export default {
  name: 'select',
  data () {
    return {
      	option: [
			{
				value: '1',
				label: '选项一'
			},
			{
				value: '2',
				label: '选项二'
			},
			{
				value: '3',
				label: '选项三'
			},
			{
				value: '4',
				label: '选项四'
			},
			{
				value: '5',
				label: '选项五'
			}
		]
    }
  },
  methods:{

  }
}
</script>
<style scoped>

</style>



