<template>
    <div class="loading-box" :class="className" v-show="show">
        <i class="loading-icon"></i>
        <p class="loading-content" v-if="content">{{content}}</p>
    </div>
</template>
<script>
  export default {
    data () {
      return {
        show: this.isShow
      }
    },
    props: {
      isShow: {
        type: Boolean,
        default: true
      },
      content: {
        type: String,
        default: '数据载入中'
      },
      className:{
        type:String
      }
    },
    watch: {
      isShow (val) {
        this.show = val
      }
    }
  }
</script>
<style lang="scss" scoped>
  .loading-box {
    position: fixed;
    z-index: 5000;
    width: 7.6em;
    min-height: 7.6em;
    top: 15px;
    left: 50%;
    margin-left: -3.8em;
    text-align: center;
    border-radius: 5px;
    color: #FFFFFF;
  }
  .loading-icon {
    width: 40px;
    height: 40px;
    display: inline-block;
    vertical-align: middle;
    -webkit-animation: Loading 1s steps(12, end) infinite;
    animation: Loading 1s steps(12, end) infinite;
    background: transparent url(data:image/svg+xml;base64,) no-repeat;
    background-size: 100%;
  }
  .loading-content {
    margin: 15px 0;
    font-size: 16px;
    color:#999;
  }
  @keyframes Loading {
    0% {
      transform: rotate3d(0, 0, 1, 0deg);
    }
    100% {
      transform: rotate3d(0, 0, 1, 360deg);
    }
  }
</style>



