<template>
  <div class="em-collapse" :collapseData="collapseData">
	<div class="em-collapse-item" v-for="(item,index) in collapseData">
		<div class="item-header">{{item.title}}</div>
		<div class="item-wrap">{{item.description}}</div>
	</div>
  </div>
</template>
<script>
export default {
  name: 'collapse',
  data () {
    return {

    }
  },
  props: {
  	collapseData: {
		type: Array
	}
  },
  methods: {

  },
  computed: {

  },
  mounted(){
  	var itemWrap = $('.item-wrap');
	for(var i in itemWrap){
		itemWrap.eq(i).height(itemWrap.eq(i).height());
	}
  	$('.em-collapse-item').click(function(){
		$(this).toggleClass('hide');
	});
  }
}
</script>
<style lang="scss" scoped>
.em-collapse{
	margin: 0 20px;
	border-top: 1px solid #e6ebf5;
	.em-collapse-item{
		.item-header{
			height: 48px;
			line-height: 48px;
			background-color: #fff;
			color: #2d2f33;
			cursor: pointer;
			font-size: 13px;
			font-weight: 500;
			transition: border-bottom-color .3s;
			outline: none;
			&:before{
				content: '';
				float: right;
				width: 5px;
				height: 5px;
				border: #b4bccc solid;
				border-width: 0 0 1px 1px;
				-webkit-transform: rotate(315deg);
				transform: rotate(315deg);
				margin: 21px 10px 0 0;
				transition: all 0.3s;
			}
		}
		.item-wrap{
			overflow: hidden;
			border-bottom: 1px solid #e6ebf5;
			padding-bottom: 25px;
			font-size: 13px;
			color: #2d2f33;
			transition: all 0.3s;
		}
		&.hide{
			.item-header{
				&:before{
					transform: rotate(225deg);
					-webkit-transform: rotate(225deg);
				}
			}
			.item-wrap{
				padding-bottom: 0;
				height: 0 !important;
			}
		}
	}
}
</style>



