<template>
  <div>
  	<h2 class="title">Tips</h2>
	<h3 class="sub">向上</h3>
    <em-button type="success" @click="showTips($event,'top',10000)">点击我显示向下的tips</em-button>
	<pre>&lt;em-button type="success" @click="showTips($event,'top',1000)"&gt;&lt;/em-button&gt;
&lt;script&gt;
export default {
  name: 'emmsgBox',
  data () {
    return {}
  },
  methods:{
    showTips($event,direction,time){
      this.$tooltip($event, {
        content: '这是一个toolTip',
        direction: direction,
        background: '#13CE66',
        color: '#fff',
        time: time
      })
     }
  }
}
&lt;/script&gt;
</pre>

