<template>
  <div class="hello">
  	<h2 class="title">文本框</h2>
  	<h3 class="sub">默认</h3>
    <em-input placeholder="请输入.." v-model="input"></em-input>
	<pre>&lt;em-input placeholder="请输入.." v-model="input"&gt;&lt;/em-input&gt;</pre>
	<h3 class="sub">禁用</h3>
    <em-input placeholder="禁止输入" disabled v-model="input"></em-input>
	<pre>&lt;em-input placeholder="禁止输入" disabled v-model="input"&gt;&lt;/em-input&gt;</pre>
	<h3 class="sub">属性</h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th>属性名</th>
			<th>类型</th>
			<th>默认值</th>
			<th>描述</th>
		</tr>
		<tr>
			<td>placeholder</td>
			<td>String</td>
			<td>请输入内容</td>
			<td>输入提示语</td>
		</tr>
		<tr>
			<td>disabled</td>
			<td>Boolean</td>
			<td>false</td>
			<td>禁用文本框</td>
		</tr>
	</table>
  </div>
</template>

<script>
import emInput from '../components/emInput.vue'
export default {
  name: 'HelloWorld',
  data () {
    return {
      input:''
    }
  },
  components: {
    "em-input" : emInput
  },
  methods:{

  }
}
</script>



