import backtoTop from './backtoTop.vue'
import emButton from './emButton.vue'
import emDialog from './emDialog.vue'
import emInput from './emInput.vue'
import emSlide from './emSlide.vue'
import emStep from './emSteps.vue'
import emSwitch from './emSwitch.vue'
import emTab from './emTab.vue'
import emTable from './emTable.vue'
import emCollapse from './emCollapse.vue'
import emRollnotice from './emRollnotice.vue'
import emSelect from './emSelect.vue'
import emCheckbox from './emCheckbox.vue'
import emCount from './emCount.vue'
import emProgress from './emProgress.vue'
import emBreadcrumb from './emBreadcrumb.vue'
import emDatepicker from './emDatepicker.vue'
import emareaSelect from './emareaSelect.vue'
import emLinkProvice from './emLinkProvice.vue'

import msgbox from './emmsgBox/index.js'
import emtips from './emTips/index.js'
export default function(Vue) {
    //全局组件
    Vue.component('em-backtotop', backtoTop)
    Vue.component('em-button', emButton)
    Vue.component('em-dialog', emDialog)
    Vue.component('em-input', emInput)
    Vue.component('em-slide', emSlide)
    Vue.component('em-step', emStep)
    Vue.component('em-switch', emSwitch)
    Vue.component('em-tab', emTab)
	  Vue.component('em-table', emTable)
    Vue.component('em-collapse', emCollapse)
    Vue.component('em-rollnotice', emRollnotice)
    Vue.component('em-select', emSelect)
  	Vue.component('em-checkbox', emCheckbox)
  	Vue.component('em-count', emCount)
  	Vue.component('em-progress', emProgress)
  	Vue.component('em-breadcrumb', emBreadcrumb)
  	Vue.component('em-datepicker', emDatepicker)
  	Vue.component('em-areaselect', emareaSelect)
  	Vue.component('em-linkprovice', emLinkProvice)
    //添加全局API
    Vue.prototype.$msgbox = msgbox
    Vue.prototype.$tooltip = emtips.installToolTip
}



