<template>
  <!-- 仿照 携程网 城市选择 -->
  <div class="emcity-select">
    <div class="search-city">
      <input type="text" placeholder="请输入城市名" v-model="citySearch" />
      <ul class="search-box">
        <li v-for="item in searchList"  @click="chosenText(item)">{{item}}</li>
      </ul>
    </div>
    <div class="city-container" id="cityBox" v-show="searchList.length==0">
      <ul>
        <li v-for="(item,index) in showCity" @click="changeTab(index)" :class="index==curIndex?'on':''">{{item.letter =='hot'?'热门城市':item.letter}}</li>
      </ul>
      <div v-for="(city,listindex) in showCity" class="city-tab" v-show="listindex==curIndex">
        <dl v-for="cityitem in city.citylist">
          <dt>{{cityitem.name=='hot'?'':cityitem.name}}</dt>
          <dd><a v-for="cityname in cityitem.citys" href="javascript:;" @click="chosenText(cityname)">{{cityname}}</a></dd>
        </dl>
      </div>
    </div>
  </div>
</template>
<script>
import showCitybyLetter from '@/../static/cityletter.js';
export default {
  name: 'emcity-select',
  data () {
    return {
      letterList:[],
      showCity: [
        { letter:'hot', citylist: [] },
        { letter:'ABCD',  citylist: [] },
        { letter:'EFGH',  citylist: [] },
        { letter:'JKLM',  citylist: [] },
        { letter:'NOPQRS', citylist: [] },
        { letter:'TUVWX', citylist: [] },
        { letter:'Y',  citylist: [] },
        { letter:'Z',  citylist: [] }
      ],
      citySearch:'',
      curIndex:0,
      searchList:[],
      curCity:''
    }
  },
  watch: {
    citySearch(curVal,oldVal){
      if(curVal!==oldVal){
        this.cityFilter(curVal);
      }
    }
  },
  created: function () {
    for (var item1 of showCitybyLetter) {
      for (var item2 of this.showCity) {
        if(item2.letter.indexOf(item1.name)>-1){
          item2.citylist.push({
            name:item1.name,
            citys:item1.citys
          });
        }
      }
    }
    console.log(this.showCity);
  },
  methods: {
    changeTab(index){
      this.curIndex=index;
    },
    cityFilter: function (searchContent) {
      if(searchContent&&(this.curCity!==searchContent)){
        this.searchList=[];
        for (var item of showCitybyLetter) {
          for (var itemname of item.citys) {
            if((itemname.indexOf(searchContent)>-1)&&(item.name!=='hot')){
              this.searchList.push(itemname);
            }
          }
        }
      }
      else {
        this.searchList=[];
      }
    },
    chosenText(name){
      this.citySearch=this.curCity=name;
      this.searchList=[];
    }
  }
}
</script>
<style lang="scss" scoped>
@import '../styles/areaselect';
</style>



