<template>
	<div class="em-count" :class="disabled?'disabled':''">
		<span class="decrease" @mouseover="inHover()" @mouseout="outHover()" @click="decrease()" :class="value==min?'disabled':''">
			<i>-</i>
		</span>
		<span class="increase" @mouseover="inHover()" @mouseout="outHover()" @click="increase()" :class="value==max?'disabled':''">
			<i>+</i>
		</span>
		<div class="count-input">
			<input type="text" autocomplete="off" readonly="readonly" v-model="value" :min="min" :max="max" :step="step" :disabled="disabled"  :class="isHover?'hover':''">
		</div>
	</div>
</template>
<script>
export default {
  name: 'count',
  data () {
    return {
		value: this.min,
		isHover: false
    }
  },
  props: {
  	min: {
		type: String,
		default: '1'
	},
	max: {
		type: String
	},
	step: {
		type: String,
		default: '1'
	},
	disabled: {
		type: Boolean
	}
  },
  methods: {
	inHover(){
		if(this.disabled){
			return;
		}
		this.isHover = true;
	},
	outHover(){
		this.isHover = false;
	},
	decrease(){
		if(this.disabled){
			return;
		}
		if(parseInt(this.value) <= parseInt(this.min)){
			return;
		}
		this.value = parseInt(this.value) - parseInt(this.step);
		if(parseInt(this.value) < parseInt(this.min)){
			this.value = this.min;
		}
	},
	increase(){
		if(this.disabled){
			return;
		}
		if(parseInt(this.value) >= parseInt(this.max)){
			return;
		}
		this.value = parseInt(this.value) + parseInt(this.step);
		if(parseInt(this.value) > parseInt(this.max)){
			this.value = this.max;
		}
	}
  },
  computed: {
	
  }
}
</script>
<style lang="scss" scoped>
.em-count{
	position: relative;
    display: inline-block;
    width: 180px;
    line-height: 38px;
	.decrease,.increase{
		position: absolute;
		z-index: 1;
		top: 1px;
		width: 40px;
		height: auto;
		text-align: center;
		background: #f5f7fa;
		color: #5a5e66;
		cursor: pointer;
		font-size: 13px;
		i{font-style: normal;}
		&.disabled{
			color: #b4bccc;
    		cursor: not-allowed;
		}
	}
	.decrease{
		left: 1px;
		border-radius: 4px 0 0 4px;
		border-right: 1px solid #d8dce5;
	}
	.increase{
		right: 1px;
		border-radius: 0 4px 4px 0;
		border-left: 1px solid #d8dce5;
	}
	.count-input{
		position: relative;
		font-size: 14px;
		display: block;
		width: 100%;
		input{
			-webkit-appearance: none;
			background-color: #fff;
			background-image: none;
			border-radius: 4px;
			border: 1px solid #d8dce5;
			box-sizing: border-box;
			color: #5a5e66;
			display: inline-block;
			font-size: inherit;
			height: 40px;
			line-height: 1;
			outline: none;
			padding: 0 15px;
			transition: border-color .2s cubic-bezier(.645,.045,.355,1);
			width: 100%;
			padding-left: 50px;
			padding-right: 50px;
			text-align: center;
			&:hover{
				border-color: #ccc;
			}
			&.hover{
				border-color: #409eff;
			}
		}
	}
	&.disabled{
		.count-input input{
			background: #f5f7fa;
			color: #b4bccc;
		}
		.decrease,.increase{
			color: #b4bccc;
			cursor: not-allowed;
		}
	}
}
</style>



