<template>
<div>
	<h2 class="title">滚动内容</h2>
	<h3 class="sub">横向滚动</h3>
	<em-rollnotice :speed="speed" :keepmove="keepmove" direction='horizontal'></em-rollnotice>
	<pre>&lt;em-rollnotice :speed="speed" :keepmove="keepmove" direction='horizontal'&gt;&lt;/em-rollnotice&gt;
&lt;script&gt;
export default {
  data (){
    return {
      speed:2,
      keepmove:true
    }
  }
}
&lt;/script&gt;
</pre>

	<h3 class="sub">纵向滚动</h3>
	<em-rollnotice :speed="speed" :line="line"></em-rollnotice>
	<pre>&lt;em-rollnotice :speed="speed" :line="line"&gt;&lt;/em-rollnotice&gt;
&lt;script&gt;
export default {
  data (){
    return {
      speed:2,
      line:2
    }
  }
}
&lt;/script&gt;
</pre>

	<h3 class="sub">纵向连续滚动</h3>
	<em-rollnotice :speed="speed" :keepmove="keepmove"></em-rollnotice>
	<pre>&lt;em-rollnotice :speed="speed" :keepmove="keepmove"&gt;&lt;/em-rollnotice&gt;
&lt;script&gt;
export default {
  data (){
    return {
      speed:2,
      keepmove:true
    }
  }
}
&lt;/script&gt;
</pre>
	<h3 class="sub">属性</h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th>属性名</th>
			<th>类型</th>
			<th>默认值</th>
			<th>描述</th>
		</tr>
		<tr>
			<td>speed</td>
			<td>Number</td>
			<td>4</td>
			<td>滚动速度</td>
		</tr>
		<tr>
			<td>rolllist</td>
			<td>Array</td>
			<td>['滚动内容一','滚动内容二','滚动内容三','滚动内容四']</td>
			<td>滚动的内容</td>
		</tr>
		<tr>
			<td>keepmove</td>
			<td>Boolean</td>
			<td>false</td>
			<td>是否持续滚动</td>
		</tr>
		<tr>
			<td>line</td>
			<td>Number</td>
			<td>1</td>
			<td>纵向滚动的窗口显示几行</td>
		</tr>
		<tr>
			<td>direction</td>
			<td>String</td>
			<td>vertical</td>
			<td>垂直或水平滚动</td>
		</tr>
	</table>
</div>
</template>
<script>
export default {
  data (){
    return {
      speed:2,
      keepmove:true,
      line:2
    }
  },
  methods:{

  },
  mounted(){

  }
}
</script>
<style scoped>

</style>



