<template>
	<label class="em-radio" :class="checked?'checked':''" :label="label" :checked="checked">
		<span class="em-radio-input">
			<span class="em-radio-inner"></span>
			<input type="radio" :value="value" />
		</span>
		<span class="em-radio-label">{{label}}</span>
	</label>
</template>
<script>
export default {
  name: 'emRadio',
  data () {
    return {

    }
  },
  props: {
  	label: {
		type: String
	},
	checked: {
		type: Boolean
	},
	value: {
		type: String
	}
  },
  mounted(){
  	$('.em-radio').click(function(){
		$(this).siblings().removeClass('checked');
		$(this).addClass('checked');
	})
  }
}
</script>
<style lang="scss" scoped>
.em-radio {
    color: #5a5e66;
    font-weight: 500;
    line-height: 1;
    position: relative;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    outline: none;
    font-size: 14px;
    -moz-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
	margin-left: 10px;
	.em-radio-input {
		white-space: nowrap;
		cursor: pointer;
		outline: none;
		display: inline-block;
		line-height: 1;
		position: relative;
		vertical-align: middle;
		.em-radio-inner {
			border: 1px solid #d8dce5;
			border-radius: 100%;
			width: 14px;
			height: 14px;
			background-color: #fff;
			position: relative;
			cursor: pointer;
			display: inline-block;
			box-sizing: border-box;
			&:after {
				width: 4px;
				height: 4px;
				border-radius: 100%;
				background-color: #fff;
				content: "";
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%,-50%) scale(0);
				transition: transform .15s cubic-bezier(.71,-.46,.88,.6);
			}
		}
		input{
			opacity: 0;
			outline: none;
			position: absolute;
			z-index: -1;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			margin: 0;
		}
	}
	.em-radio-label {
		font-size: 14px;
		padding-left: 5px;
	}
	&.checked{
		.em-radio-inner {
			border-color: #409eff;
			background: #409eff;
			&:after {
				transform: translate(-50%,-50%) scale(1);
			}
		}
		.em-radio-label {
			color: #409eff;
		}
	}
}
</style>



