<template>
  <div>
	<div class="switch-wrap" :class="classStr" :disabled="disabled" @click="handleSwitch()">
		<span></span>
	</div>
  </div>
</template>
<script>
export default {
  name: 'switch',
  data () {
    return {
		    isActive: false
    }
  },
  props: {
  	disabled: {
      type:Boolean,
      default:false
    }
  },
	methods: {
		handleSwitch(){
			if(this.disabled){
				return;
			}
			this.isActive = !this.isActive;
		}
	},
	computed: {
    	classStr(){
			var className = [this.isActive?'active':'',this.disabled?'disabled':''];
			return className.join(" ");
		}
	}
}
</script>
<style lang="scss" scoped>
.switch-wrap{
    display: inline-block;
    position: relative;
    width: 40px;
    height: 20px;
    border: 1px solid #ff4949;
    outline: none;
    border-radius: 10px;
    box-sizing: border-box;
    background: #ff4949;
    cursor: pointer;
    transition: border-color .3s,background-color .3s;
    vertical-align: middle;
	span{
		position: absolute;
		top: 1px;
		left: 1px;
		border-radius: 100%;
		transition: transform .3s;
		width: 16px;
		height: 16px;
		background-color: #fff;
	}
	&.active{
		border-color: #13ce66;
		background-color: #13ce66;
		span{
			transform: translate3d(20px, 0px, 0px);
		}
	}
	&.disabled{
		border-color: #ccc;
		background-color: #ccc;
		cursor: not-allowed;
		opacity: .8;
	}
}
</style>



