<template>
    <transition name="em">
      <div v-if="show" class="em-confirm">
        <div class="em-box">
          <div class="em-boxinner">
              <div class="em-title" v-if="title">{{title}}</div>
              <div class="em-content">{{content}}</div>
          </div>
          <div class="em-boxbuttons">
              <span class="em-btnsuccess" @click.stop="successBtn">{{confirm}}</span>
              <span class="em-btncancel" v-if="cancel" @click.stop="cancelBtn">{{cancel}}</span>
          </div>
        </div>
      </div>
    </transition>
</template>
<script>
export default {
  data () {
    return {
      show: false,
      title: '提示',
      content: '',
      confirm: '确定',
      cancel:''
    }
  },
  methods: {
    successBtn () {
        this.show = false;
    },
    cancelBtn () {
        this.show = false;
    }
  }
}
</script>
<style lang="scss">
.em-confirm {
    background: rgba(0, 0, 0, 0.5);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 9999;
    .em-box {
        width: 80%;
        max-width: 400px;
        top: 200px;
        position: absolute;
        left: 0;
        right: 0;
        margin: auto;
        background: #fff;
        box-sizing: border-box;
        padding: 20px;
        border-radius: 6px;
    }
    .em-title {
        padding-left: 0;
        margin-bottom: 0;
        font-size: 16px;
        font-weight:bold;
        height: 18px;
        color: #333;
        user-select: none;
    }
    .em-content {
        padding: 14px 0;
        line-height: 24px;
        color: #48576a;
        font-size: 14px;
        user-select: none;
    }
    .em-boxbuttons {
        text-align: right;
    }
    .em-btnsuccess {
        background: #20a0ff;
        border-color: #20a0ff;
        display: inline-block;
        line-height: 1;
        white-space: nowrap;
        cursor: pointer;
        color: #fff;
        margin: 0;
        padding: 10px 15px;
        border-radius: 4px;
    }
    .em-btncancel {
        display: inline-block;
        line-height: 1;
        white-space: nowrap;
        cursor: pointer;
        background: #fff;
        border: 1px solid #c4c4c4;
        color: #1f2d3d;
        margin: 0;
        padding: 10px 15px;
        border-radius: 4px;
    }
}

</style>



