<template>
  <div class="btn-box" :class="className" @click="gototop()">
    	<a class="btn-text" href="javascript:;"></a>
	</div>
</template>
<script>
export default {
  name:'embacktotop',
  mounted () {
    $(window).on('scroll', function(){
      if ($(this).scrollTop() > 0) {
        $('.btn-box').fadeIn()
      } else {
        $('.btn-box').fadeOut()
      }
    })
  },
  methods: {
    gototop () {
      $('body,html').animate({ scrollTop: 0 }, 800)
    }
  },
  props: {
    className : {
      type : String
    }
  }
}
</script>
<style scoped lang="scss">
.btn-box{
  position: fixed;
	bottom: 10px;
	right:10px;
	display: none;
  width:70px;
  height:70px;
  line-height:100px;
  background:rgba(0,0,0,0.75);
  border-radius:50%;
  text-align:center;
  cursor: pointer;
  .btn-text{
    display: inline-block;
    width:26px;
    height:26px;
    border: solid #fff;
    border-width: 4px 4px 0 0;
    transform:rotate(-45deg);
  }
}
</style>



