<template>
  <div class="msg-box" v-show="isShow">
    <div class="dialog-header">
      <span class="dialog-title">提示</span>
      <span class="dialog-headerbtn">
        <i class="icon-close">×</i>
      </span>
    </div>
    <div class="dialog-body"><span>some text...</span></div>
    <div class="dialog-footer">
        <button type="button" class="em-button button-default" @click="cancelBox()">
          <span>取 消</span>
        </button>
        <button type="button" class="em-button button-primary" @click="confirmBox()">
            <span>确 定</span>
        </button>
    </div>
	</div>
</template>
<script>
export default {
  name:'emdialog',
  props: {
    isShow : {
      type:Boolean,
      default:false
    }
  },
  methods: {
    cancelBox(){
      this.$emit('cancel')
    },
    confirmBox(){
      this.$emit('confirm')
    }
  },
  watch : {
    isShow (val) {
      if (val) {
        $("body").addClass("layer");
      }else{
        $("body").removeClass("layer");
      }
    }
  },
  destroyed(){
    if($("body").hasClass("layer")){
      $("body").removeClass("layer");
    }
  }
}
</script>
<style scoped lang="scss">
.msg-box{
  position:  absolute;
  top:20%;
  left:0;right:0;
  transform:translateY(-50%);
  width: 575px;
  height: 200px;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 1px 3px rgba(0,0,0,.3);
  margin:0 auto;
  .dialog-header{
    padding: 15px 15px 10px;
    position: relative;
    .dialog-title{
      line-height: 24px;
      font-size: 18px;
      color: #2d2f33;
    }
    .dialog-headerbtn{
      position: absolute;
      top: 15px;
      right: 15px;
      padding: 0;
      background: transparent;
      border: none;
      outline: none;
      cursor: pointer;
      font-size: 16px;
      i{
        font-style: normal;
        color: #878d99;
      }
    }
  }
  .dialog-body{
    padding: 30px 20px;
    color: #5a5e66;
    line-height: 24px;
    font-size: 14px;
  }
  .dialog-footer{
    padding: 15px;
    padding-top: 10px;
    text-align: right;
    box-sizing: border-box;
    .em-button{
      display: inline-block;
      line-height: 1;
      cursor: pointer;
      background: #fff;
      border: 1px solid #d8dce5;
      border-color: #d8dce5;
      color: #5a5e66;
      text-align: center;
      box-sizing: border-box;
      outline: none;
      margin: 0;
      transition: .1s;
      padding: 12px 20px;
      font-size: 14px;
      border-radius: 4px;
      user-select: none;
      &:first-child {
        margin-right: 10px;
      }
    }
  }
}

</style>



