<template>
  <div class="em-tab" :class="classStr">
    <ul class="tab-list">
      <div class="active-bar" v-show="type=='default'"></div>
      <li v-for="(item, index) in tabItem" :class="{active: index === actIndex}" v-html="item"></li>
    </ul>
    <slot></slot>
  </div>
</template>
<script>
export default {
  name: 'tab',
  data() {
      return {
        actIndex: this.activedIndex || 0
      }
  },
  props: {
    tabItem:{
      type : Array ,
      default: ()=> ['选项一','选项二','选项三']
    },
    activedIndex:{
      type:Number,
      default:0
    },
    type:{
      type:String,
      default:'default'
    },
    triggerType:{
      type:String,
      default:'click'
    }
  },
  mounted(){
    var that=this;
    that.handelSwitch(that.actIndex);
    //事件名称写错默认为 click
    const trigger=(that.triggerType!=='click'&&that.triggerType!=='mouseover')?'click':that.triggerType;
    $(that.$el).find("li").bind(trigger,function(){
      that.handelSwitch($(this).index()-1);
    })
  },
	methods: {
    handelSwitch(index){
      var $curbox=$(this.$el);
      this.actIndex=index;
      if(this.type=="default"){
        const $tabli=$curbox.find(".tab-list li");
        var widthlen=$tabli.eq(index).width(),
            paddinglen=parseInt($tabli.css("padding").replace(/[^0-9]/ig,""))/2,
            translateXlen=paddinglen;
        for (var i = 0; i < index; i++) {
          translateXlen+=($tabli.eq(i).innerWidth());
        }
        translateXlen+=paddinglen;
        $(this.$el).find(".active-bar").css({"width":widthlen,"transform":`translateX(${translateXlen}px`});
      }
      $(this.$el).children().not(".tab-list").siblings().eq(index+1).show().siblings().not(".tab-list").hide();
    },
    typeClass(type){
      switch (type) {
        case 'card':
          return 'card-tab';break;
        case 'border-card':
          return 'bordercard-tab';break;
        default:
          return ;
      }
    }
	},
  computed:{
    classStr() {
      var className=[];
      if(this.type){
        className.push(this.typeClass(this.type));
      }
      return className.join(" ");
    }
  }
}
</script>
<style lang="scss" scoped>
.em-tab{
  ul{
    position: relative;
    display: inline-block;
    transform: translateX(0);
    li{
      position: relative;
      padding: 0 20px;
      height: 40px;
      line-height: 40px;
      display: inline-block;
      font-size: 14px;
      color: #2d2f33;
      cursor: pointer;
      user-select: none;
      &.active{
        color: #409eff;
      }
    }
    .active-bar {
      position: absolute;
      bottom: 0;
      left: 0;
      height: 2px;
      background-color: #409eff;
      z-index: 1;
      transition: transform .3s cubic-bezier(.645,.045,.355,1);
    }
  }
}
.card-tab{
  border: 1px solid #dfe4ed;
  border-bottom: none;
  li{
    border-bottom: 1px solid #dfe4ed;
    border-left: 1px solid #dfe4ed;
    &:first-of-type{
      border-left: 0;
    }
    &.active{
      border-bottom: 1px solid transparent;
    }
  }
}

</style>



