<template>
  <div id="app">
  	<ul class="nav-list">
  		<li v-for="(item, index) in router" @click="chosenRouter(index)" :class="routerIndex==index?'current':''">
  		  <router-link :to="item.path">{{item.name}}</router-link>
  		</li>
  	</ul>
  	<div class="router-wrap">
      <keep-alive>
  		    <router-view></router-view>
      </keep-alive>
  	</div>
  </div>
</template>

<script>
import router from './router/index'
export default {
  name: 'app',
  data () {
    return {
      router: router,
	    routerIndex: 0
    }
  },
  methods:{
	chosenRouter(index){
		this.routerIndex = index;
	}
  }
}
</script>

<style lang="scss" scpoed>
.nav-list{
	position: fixed;
    top: 0;
    left: 0;
    width: 250px;
    height: 100%;
    overflow-y: auto;
    border-right: 1px solid #ccc;
	li{
		a{
			display: block;
			padding: 8px 0;
			font-size: 14px;
			text-align: center;
			cursor: pointer;
			color: #666;
			font-size: 18px;
			&:hover{color: #fc9153;}
		}
		&.current{
			a{color: #fc9153;}
		}
		&:hover{
		  background:rgb(67,74,80);
		}
	}
}
.router-wrap{
	margin-left: 250px;
    padding: 20px;
}
.title,.sub{padding: 20px 0;}
pre{
	border: 1px dotted #785;
    background: #f5f5f5;
    font-family: Consolas;
    padding: 10px;
    margin: 20px 0 0;
    line-height: 20px;
    font-size: 14px;
}
.props{
	width: 100%;
	border-left: 1px solid #d4d4d4;
	border-top: 1px solid #d4d4d4;
	tr{
		th,td{
			text-align: center;
			font-size: 14px;
			padding: 10px 0;
			border-right: 1px solid #d4d4d4;
			border-bottom: 1px solid #d4d4d4;
		}
		th{
			background: #f5f5f5;
		}
	}
}
</style>



