<template>
  <div>
	<ul class="em-breadcrumb" :linkList="linkList" :separator="separator">
		<li v-for="(item,index) in linkList">
			<a :href="item.link">{{item.name}}</a>
			<span>{{separator}}</span>
		</li>
	</ul>
  </div>
</template>
<script>
export default {
  name: 'breadcrumb',
  data () {
    return {
		
    }
  },
  props: {
  	linkList: {
		type: Array
	},
	separator: {
		type: String,
		default: '/'
	},
	href: {
		type: String,
	}
  },
  methods: {
	
  },
  computed: {
	
  }
}
</script>
<style lang="scss" scoped>
.em-breadcrumb{
	font-size: 0;
	li{
		font-size: 14px;
		display: inline-block;
		vertical-align: middle;
		box-sizing: border-box;
		a{
			font-weight: 700;
			transition: color .2s cubic-bezier(.645,.045,.355,1);
			color: #2d2f33;
			&:hover{color: #409eff;}
		}
		span{
			margin: 0 9px;
			font-weight: 700;
			color: #b4bccc;
		}
		&:last-of-type{
			a{
				font-weight: normal;
				cursor: text;
				&:hover{color: #2d2f33;}
			}
			span{display: none;}
		}
	}
}
</style>



