<template>
  <div class="">
  	<h2 class="title">消息弹框</h2>
  	<h3 class="sub">默认</h3>
    <label id="msgbox" @click.stop="showMsgbox">点击我显示msgbox</label>
	<pre>&lt;label id="msgbox" @click.stop="showMsgbox"&gt;点击我显示msgbox&lt;/label&gt;
&lt;script&gt;
export default {
  name: 'emmsgBox',
  data () {
    return {
      showbox:false
    }
  },
  methods:{
    showMsgbox(){
      this.$msgbox({
          title:'温馨提示',
          content:'这里是消息弹出内容',
          confirm:'确定按钮'
      }).then(()=>{
          alert("我点击了确定按钮")
      }).catch((err)=>{
        console.log("error");
      })
    }
  }
}
&lt;/script&gt;
</pre>
  	<h3 class="sub">含取消功能</h3>
    <label id="msgbox2" @click.stop="showMsgbox2">点击我显示msgbox2</label>
	<pre>&lt;label id="msgbox2" @click.stop="showMsgbox2"&gt;点击我显示msgbox2&lt;/label&gt;
&lt;script&gt;
export default {
  name: 'emmsgBox',
  data () {
    return {
      showbox:false
    }
  },
  methods:{
    showMsgbox2(){
      this.$msgbox({
          title:'温馨提示',
          content:'这里是消息弹出内容',
          confirm:'确定哈',
          cancel:'取消按钮'
      }).then(()=>{
          alert("我点击了确定按钮")
      }).catch((err)=>{
        console.log("error");
      })
    }
  }
}
&lt;/script&gt;
</pre>
  </div>
</template>

<script>
export default {
  name: 'emmsgBox',
  data () {
    return {
      showbox:false
    }
  },
  methods:{
    showMsgbox(){
      this.$msgbox({
          title:'温馨提示',
          content:'这里是消息弹出内容',
          confirm:'确定按钮'
      }).then(()=>{
          alert("我点击了确定按钮")
      }).catch((err)=>{
        console.log("error");
      })
    },
    showMsgbox2(){
      this.$msgbox({
          title:'温馨提示',
          content:'这里是消息弹出内容',
          confirm:'确定哈',
          cancel:'取消按钮'
      }).then(()=>{
          alert("我点击了确定按钮")
      }).catch((err)=>{
        console.log("error");
      })
    }
  },
  mounted(){

  }
}
</script>
<style scoped>

</style>



