<template>
  <div class="slide-container">
    <ul class="slide-list">
      <li v-for="(item, i) in slideItem" @click="handleClickparent(i)"><a href="javascript:;">{{item.name}}<span class="list-icon">{{i==indexParent?'-':'+'}}</span></a>
        <ul class="slide-content" v-show="i==indexParent">
          <li v-for="(itemname, j) in item.namelist" @click="handleClickchild(i,j)">
            <a href="javascript:;" :class="j==indexChild&&level==i?'child-chosen':''">{{itemname}}</a>
          </li>
        </ul>
      </li>
    </ul>
	</div>
</template>
<script>
export default {
  name: 'slide',
  data () {
    return {
      indexParent:0,
      indexChild:0,
      level:0
    }
  },
	props:{
    slideItem:{
      type : Array ,
      default: ()=> [
          {'name':'选卡1','namelist':['选卡1内容1','选卡1内容2','选卡1内容3']},
          {'name':'选卡2','namelist':['选卡2内容1','选卡2内容2','选卡2内容3']},
          {'name':'选卡3','namelist':['选卡3内容1','选卡3内容2','选卡3内容3']}
      ]
    }
  },
	methods: {
    handleClickparent(i){
      if(this.indexParent==i){
        return ;
      }
      this.indexParent=i;
    },
    handleClickchild(i,j){
      this.level=i;
      this.indexChild=j;
    }
	}
}
</script>
<style lang="scss" scoped>
.slide-list{
  list-style: none;
  width: 200px;
  li{
    position: relative;
    border:2px solid #D4D4D4;
    display: inline-block;
    width: 200px;
    height: 35px;
    line-height: 35px;
    margin-bottom: 5px;
    a{
      padding-left: 15px;
    }
    .list-icon{
      right: 7px;
      top:7px;
      position: absolute;
      display: inline-block;
      width: 15px;
      height: 15px;
      border:1px solid #D4D4D4;
      line-height: 15px;
      text-align: center;
    }
  }
}
.slide-content {
  overflow: hidden;
  margin-top: 5px;
  li{
    margin: 0;
    border:0;
    height: 35px;
    line-height: 35px;
    a{
      padding-left: 25px;
      display: block;
    }
  }
  .child-chosen{
    background: #2F5895;
    color:#fff;
  }
}

</style>



