<template>
  <div>
  	<h2 class="title">单选框</h2>
    <em-radio label="首选项" value="1" checked></em-radio>
  	<em-radio label="备选项" value="2"></em-radio>
	<em-radio label="其他项" value="3"></em-radio>
	<pre>&lt;em-radio label="首选项" value="1" checked&gt;&lt;/em-radio&gt;
&lt;em-radio label="备选项" value="2"&gt;&lt;/em-radio&gt;
&lt;em-radio label="其他项" value="3"&gt;&lt;/em-radio&gt;</pre>
	<h3 class="sub">属性</h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th>属性名</th>
			<th>类型</th>
			<th>默认值</th>
			<th>描述</th>
		</tr>
		<tr>
			<td>label</td>
			<td>String</td>
			<td>null</td>
			<td>选项名</td>
		</tr>
		<tr>
			<td>value</td>
			<td>String</td>
			<td>null</td>
			<td>选项值</td>
		</tr>
		<tr>
			<td>checked</td>
			<td>Boolean</td>
			<td>false</td>
			<td>已选状态</td>
		</tr>
	</table>
  </div>
</template>

<script>
import emRadio from '../components/emRadio.vue'
export default {
  name: 'emRadio',
  data () {
    return {

    }
  },
  components: {
    "em-radio" : emRadio
  },
  methods:{

  }
}
</script>
