<template>
  <label class="em-checkbox" :class="classStr" :checked="checked" :disabled="disabled">
  	<span class="em-checkbox-input">
		<span class="em-checkbox-inner" @click="chosenCheckbox()"></span>
		<input type="checkbox" :value="value">
	</span>
	<span class="em-checkbox-label" @click="chosenCheckbox()">{{label}}</span>
  </label>
</template>
<script>
export default {
  name: 'checkbox',
  data () {
    return {
		
    }
  },
  props: {
  	value: {
		type: String
	},
	label: {
		type: String
	},
	checked: {
		type: Boolean
	},
	disabled: {
		type: Boolean
	}
  },
  methods: {
	chosenCheckbox(){
		var emCheckbox = $(this.$el);
		if(emCheckbox.hasClass('disabled')){
			return;
		}
		emCheckbox.toggleClass('checked');
	}
  },
  computed: {
	classStr(){
		var className = [this.checked?'checked':'',this.disabled?'disabled':''];
		return className.join(' ');
	}
  }
}
</script>
<style lang="scss" scoped>
.em-checkbox{
	color: #5a5e66;
    font-weight: 500;
    font-size: 14px;
    position: relative;
    cursor: pointer;
    display: inline-block;
    white-space: nowrap;
    user-select: none;
	margin-left: 10px;
	font-size: 0;
	.em-checkbox-input{
		white-space: nowrap;
		cursor: pointer;
		outline: none;
		display: inline-block;
		line-height: 1;
		position: relative;
		vertical-align: middle;
		.em-checkbox-inner{
			display: inline-block;
			position: relative;
			border: 1px solid #d8dce5;
			border-radius: 2px;
			box-sizing: border-box;
			width: 14px;
			height: 14px;
			background-color: #fff;
			z-index: 1;
			transition: border-color .25s cubic-bezier(.71,-.46,.29,1.46),background-color .25s cubic-bezier(.71,-.46,.29,1.46);
			&:after{
				content: "";
				box-sizing: content-box;
				border: 1px solid #fff;
				border-left: 0;
				border-top: 0;
				height: 7px;
				left: 4px;
				position: absolute;
				top: 1px;
				transform: rotate(45deg) scaleY(0);
				width: 3px;
				transition: transform .15s cubic-bezier(.71,-.46,.88,.6) .05s;
				transform-origin: center;
			}
			&:hover{
				border-color: #409eff;
			}
		}
		input{
			opacity: 0;
			outline: none;
			position: absolute;
			margin: 0;
			width: 0;
			height: 0;
			left: -999px;
		}
	}
	.em-checkbox-label{
		display: inline-block;
		padding-left: 5px;
		line-height: 20px;
		font-size: 14px;
		vertical-align: middle;
	}
	&.disabled{
		.em-checkbox-inner{
			background-color: #edf2fc !important;
			border-color: #d8dce5 !important;
			cursor: not-allowed;
			&:after{
				cursor: not-allowed;
				border-color: #b4bccc;
			}
			&:hover{
				border-color: #d8dce5;
			}
		}
		.em-checkbox-label{
			color: #b4bccc !important;
			cursor: not-allowed;
		}
	}
	&.checked{
		.em-checkbox-inner{
			background-color: #409eff;
			border-color: #409eff;
			&:after{
				transform: rotate(45deg) scaleY(1);
			}
		}
		.em-checkbox-label{
			color: #409eff;
		}
	}
}
</style>



