<template>
  <div class="em-select" :class="isOpened?'opened':''">
  	<input type="text" v-model="value" :placeholder="placeholder" readonly="readonly" :class="disabled?'disabled':''" :disabled="disabled" autocomplete="off" @click="toggleSelect()" />
    <div class="select-list" v-show="option.length>0">
      <ul>
    		<li v-for="(item,index) in option" @click="chosenOption(item.label)">{{item.label}}</li>
    	</ul>
    </div>
  </div>
</template>
<script>
export default {
  name: 'select',
  data () {
    return {
  		isOpened: false,
  		value:''
    }
  },
  props: {
    option: {
  		type: Array,
      default:()=>[]
  	},
  	disabled: {
  		type: Boolean
  	},
    placeholder: {
    	type:String,
    	default:'请输入内容'
    }
  },
  methods: {
  	toggleSelect(){
  		this.isOpened = !this.isOpened;
  	},
  	chosenOption(label){
  		this.value = label;
  		this.isOpened = false;
      this.$emit("click",label);
  	}
  },
  computed: {
  	classStr(){
  		var className = [this.isOpened?'opened':'',this.disabled?'disabled':''];
  		return className.join(' ');
  	}
  },
  mounted(){
  	var that = this;
  	$(document).click(function(e){
  		if($(e.target).parents('.em-select').length == 0){
  			that.isOpened = false;
  		}
  	});
  }
}
</script>
<style lang="scss" scoped>
.em-select{
	position: relative;
	display: inline-block;
	width: 240px;
	font-size: 14px;
	&:after{
		content: '';
		position: absolute;
		top: 14px;
		right: 12px;
		width: 7px;
		height: 7px;
		border: #b4bccc solid;
		border-width: 0 0 1px 1px;
		-webkit-transform: rotate(315deg);
		transform: rotate(315deg);
		-webkit-transition: all 0.3s;
		transition: all 0.3s;
		-webkit-transform-origin: 2px 6px;
		transform-origin: 2px 6px;
	}
	&.opened{
		&:after{
			-webkit-transform: rotate(135deg);
			transform: rotate(135deg);
		}
		.select-list{
			-webkit-transform: scaleY(1);
			transform: scaleY(1);
			opacity: 1;
		}
	}
	input{
		-webkit-appearance: none;
		border-radius: 4px;
		border: 1px solid #d8dce5;
		box-sizing: border-box;
		color: #5a5e66;
		font-size: inherit;
		height: 40px;
		line-height: 1;
		outline: none;
		padding: 0 30px 0 15px;
		transition: border-color .2s cubic-bezier(.645,.045,.355,1);
		width: 100%;
		cursor: pointer;
		&:hover{
			border-color: #b4bccc;
		}
		&:focus{
			border-color: #409eff;
		}
		&.disabled{
			cursor: not-allowed;
		}
	}
  .select-list{
    position: absolute;
    width: 100%;
    z-index: 1000;
    background: #fff;
    left: 0;
    top: 55px;
    opacity: 0;
    -webkit-transform: scaleY(0);
    transform: scaleY(0);
    -webkit-transform-origin: top;
		transform-origin: top;
    box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
    transition: all 0.3s;
    ul{
  		border: 1px solid #dfe4ed;
      padding: 5px 0;
      border-radius: 4px;
      max-height: 245px;
      overflow-y: auto;
      &::-webkit-scrollbar{//滚动条整体部分
        width: 3px;
        height:10px;
        background:rgba(0,0,0,0.5);
      }
      &::-webkit-scrollbar-track{//scroll轨道背景
        -webkit-box-shadow: inset 0 0 6px #eee;
        border-radius: 10px;
        background:#ccc;
      }
      &::-webkit-scrollbar-thumb{ //滚动条中能上下移动的小块
        border-radius: 10px;
        -webkit-box-shadow: inset 0 0 6px #eee;
        background-color:rgba(0,0,0,0.5);
      }
  		li{
  			font-size: 14px;
  			padding: 0 20px;
  			white-space: nowrap;
  			overflow: hidden;
  			text-overflow: ellipsis;
  			color: #5a5e66;
  			height: 35px;
  			line-height: 35px;
  			box-sizing: border-box;
  			cursor: pointer;
  			&:hover {
  				background-color: #f5f7fa;
  			}
  		}
      &:before{
        position: absolute;
        top: -7px;
        left: 80px;
        display: inline-block;
        border-right: 7px solid transparent;
        border-bottom: 7px solid #dfe4ed;
        border-left: 7px solid transparent;
        border-bottom-color:#dfe4ed;
        content: '';
      }
      &:after{
        position: absolute;
        top: -5px;
        left: 81px;
        display: inline-block;
        border-right: 6px solid transparent;
        border-bottom: 6px solid #ffffff;
        border-left: 6px solid transparent;
        content: '';
      }
  	}
  }

}
</style>



