function pubtimeFormate(ftime,formate="-"){
  let nowDate = new Date(),
      nowyear = nowDate.getFullYear(),
      nowmonth = nowDate.getMonth() + 1,
      nowday = nowDate.getDate(),
      nowhour = nowDate.getHours(),
      ayear,
      amonth,
      aday,
      ahour,
      arrtime=[];
  if(typeof ftime=='number'){
    //对时间格式拆分 ftime格式 为时间戳
    ftime=parseInt(ftime);
    ftime = new Date(ftime);
    ayear = ftime.getFullYear();
    amonth = (ftime.getMonth()+1 < 10 ? '0'+(ftime.getMonth()+1) : ftime.getMonth()+1);
    aday = ftime.getDate();
    ahour = ftime.getHours();
  }
  else{
    //对时间格式拆分 ftime格式 2017-11-22
    ftime = ftime + '';
    arrtime = ftime.split(formate),
    ayear = parseInt(ftime.substr(0, 4)),
    amonth = parseInt(ftime.substr(5, 2)),
    aday = parseInt(ftime.substr(8, 2)),
    ahour = parseInt(ftime.substr(11, 2));
  }
  if ((ayear !== nowyear)) {
    if(typeof ftime=='number'){
      return [ayear, amonth, aday].join(formate);
    }
    return ftime;
  }
  if ((ayear == nowyear) && (nowmonth !== amonth)) {
    if(typeof ftime=='number'){
      return [ayear, amonth, aday].join(formate);
    }
    return ftime;
  }
  if ((ayear == nowyear) && (nowday !== aday)) {
    if (nowday - aday>30){
      if(typeof ftime=='number'){
        return [ayear, amonth, aday].join(formate);
      }
      return ftime;
    }
    return (nowday - aday) + '天前';
  }
  if ((nowmonth == amonth) && (nowday == aday)) {
    if (nowhour == ahour) {
      return '刚刚'
    }
    else {
      return (nowhour - ahour) + '小时前';
    }
  }
  else {
    return ftime;
  }
}
function showEllipsis(str,len=10,ellipsis="true"){
  if (str == null) {
    return '';
  }
  if (str.length <= len) {
    return str;
  }
  let subtext = str.substr(0,len);
  if (ellipsis && len > 0) {
    subtext += '...'
  }
  return subtext;
}
module.exports = {
  pubtimeFormate,
  showEllipsis
}



