<template>
  <div>
  	<h2 class="title">选项卡</h2>
	<h3 class="sub">默认</h3>
	<em-tab>
      <div>我是index=0的选项卡</div>
      <div>我是index=1的选项卡</div>
      <div>我是index=2的选项卡</div>
    </em-tab>
<pre>
&lt;em-tab&gt;
  &lt;div&gt;我是index=0的选项卡&lt;div&gt;
  &lt;div&gt;我是index=1的选项卡&lt;div&gt;
  &lt;div&gt;我是index=2的选项卡&lt;div&gt;
&lt;/em-tab&gt;
</pre>

	<h3 class="sub">预加载</h3>
    <em-tab :activedIndex="activedIndex" :tabItem="tabItem">
      <div>我是index=0的选项卡</div>
      <div>我是index=1的选项卡</div>
      <div>我是index=2的选项卡</div>
      <div>我是index=3的选项卡</div>
      <div>我是index=4的选项卡</div>
    </em-tab>
<pre>
&lt;em-tab :activedIndex="activedIndex" :tabItem="tabItem"&gt;
  &lt;div&gt;我是index=0的选项卡&lt;div&gt;
  &lt;div&gt;我是index=1的选项卡&lt;div&gt;
  &lt;div&gt;我是index=2的选项卡&lt;div&gt;
  &lt;div&gt;我是index=3的选项卡&lt;div&gt;
  &lt;div&gt;我是index=4的选项卡&lt;div&gt;
&lt;/em-tab&gt;
&lt;script&gt;
export default {
  name: 'tab',
  data () {
	return {
	  activedIndex:1,
	  tabItem:['选项一','选项二','选项三','选项四','选项五']
	}
  }
}
&lt;/script&gt;
</pre>

	<h3 class="sub">带边框的选项卡</h3>
    <em-tab :activedIndex="activedIndex2" :tabItem="tabItem2" type="card">
      <div>我是index=0的选项卡</div>
      <div>我是index=1的选项卡</div>
      <div>我是index=2的选项卡</div>
      <div>我是index=3的选项卡</div>
    </em-tab>
<pre>
&lt;em-tab :activedIndex="activedIndex2" :tabItem="tabItem2" type="card"&gt;
  &lt;div&gt;我是index=0的选项卡&lt;div&gt;
  &lt;div&gt;我是index=1的选项卡&lt;div&gt;
  &lt;div&gt;我是index=2的选项卡&lt;div&gt;
  &lt;div&gt;我是index=3的选项卡&lt;div&gt;
  &lt;div&gt;我是index=4的选项卡&lt;div&gt;
&lt;/em-tab&gt;
&lt;script&gt;
export default {
  name: 'tab',
  data () {
	return {
	  activedIndex2:1,
	  tabItem2:['区域一','区域二','区域三','区域四']
	}
  }
}
&lt;/script&gt;
</pre>

  	<h3 class="sub">鼠标悬浮进行切换</h3>
    <em-tab triggerType="mouseover" :activedIndex="activedIndex2" :tabItem="tabItem2" type="card">
      <div>我是index=0的选项卡</div>
      <div>我是index=1的选项卡</div>
      <div>我是index=2的选项卡</div>
      <div>我是index=3的选项卡</div>
    </em-tab>
<pre>
&lt;em-tab triggerType="mouseover" :activedIndex="activedIndex2" :tabItem="tabItem2" type="card"&gt;
  &lt;div&gt;我是index=0的选项卡&lt;div&gt;
  &lt;div&gt;我是index=1的选项卡&lt;div&gt;
  &lt;div&gt;我是index=2的选项卡&lt;div&gt;
  &lt;div&gt;我是index=3的选项卡&lt;div&gt;
  &lt;div&gt;我是index=4的选项卡&lt;div&gt;
&lt;/em-tab&gt;
&lt;script&gt;
export default {
  name: 'tab',
  data () {
	return {
	  activedIndex2:1,
	  tabItem2:['区域一','区域二','区域三','区域四']
	}
  }
}
&lt;/script&gt;
</pre>

	<h3 class="sub">属性</h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th>属性名</th>
			<th>类型</th>
			<th>默认值</th>
			<th>描述</th>
		</tr>
		<tr>
			<td>activedIndex</td>
			<td>Number</td>
			<td>0</td>
			<td>当前选中的选项卡序号</td>
		</tr>
		<tr>
			<td>tabItem</td>
			<td>Array</td>
			<td>['选项一','选项二','选项三']</td>
			<td>选项卡名称</td>
		</tr>
		<tr>
			<td>type</td>
			<td>String</td>
			<td>default</td>
			<td>带边框的选项卡</td>
		</tr>
		<tr>
			<td>triggerType</td>
			<td>String</td>
			<td>click</td>
			<td>鼠标悬浮式的选项卡</td>
		</tr>
	</table>
  </div>
</template>
<script>
export default {
  name: 'tab',
  data () {
    return {
      activedIndex:1,
      tabItem:['选项一','选项二','选项三','选项四','选项五'],
      activedIndex2:1,
      tabItem2:['区域一','区域二','区域三','区域四']
    }
  }
}
</script>
<style lang="scss" scoped>

</style>



