<template>
  <div class="">
  	<h2 class="title">对话弹框</h2>
    <label id="msgbox" @click="showMsgbox">点击我显示dialog</label>
    <em-dialog :isShow="showbox" @cancel="cancel()" @confirm="confirm()"></em-dialog>
	<pre>&lt;label id="msgbox" @click="showMsgbox"&gt;点击我显示dialog&lt;/label&gt;
&lt;em-dialog :isShow="showbox" @cancel="cancel()" @confirm="confirm()"&gt;&lt;/em-dialog&gt;
&lt;script&gt;
import emDialog from '../components/emDialog.vue'
export default {
  name: 'emDialog',
  data () {
    return {
      showbox:false
    }
  },
  components: {
    "em-dialog" : emDialog
  },
  methods:{
    showMsgbox(){
      this.showbox=true;
    },
    cancel(){
      alert("我点击取消啦");
      this.showbox=false;
    },
    confirm(){
      alert("我点击确定啦")
    }
  }
}
&lt;/script&gt;
</pre>
  </div>
</template>

<script>
import emDialog from '../components/emDialog.vue'
export default {
  name: 'emDialog',
  data () {
    return {
      showbox:false
    }
  },
  components: {
    "em-dialog" : emDialog
  },
  methods:{
    showMsgbox(){
      this.showbox=true;
    },
    cancel(){
      alert("我点击取消啦");
      this.showbox=false;
    },
    confirm(){
      alert("我点击确定啦")
    }
  }
}
</script>
<style scoped>

</style>



