<template>
  <div>
  	<h2 class="title">复选框</h2>
    <em-checkbox label="选项A" value="1" checked></em-checkbox>
	<em-checkbox label="选项B" value="2" checked></em-checkbox>
	<em-checkbox label="选项C" value="3"></em-checkbox>
	<em-checkbox label="禁用" value="4" disabled></em-checkbox>
	<em-checkbox label="选中且禁用" value="5" checked disabled></em-checkbox>
	<pre>&lt;em-checkbox label="选项A" value="1" checked&gt;&lt;/em-checkbox&gt;
&lt;em-checkbox label="选项B" value="2" checked&gt;&lt;/em-checkbox&gt;
&lt;em-checkbox label="选项C" value="3"&gt;&lt;/em-checkbox&gt;
&lt;em-checkbox label="禁用" value="4" disabled&gt;&lt;/em-checkbox&gt;
&lt;em-checkbox label="选中且禁用" value="5" checked disabled&gt;&lt;/em-checkbox&gt;
</pre>
	<h3 class="sub">属性</h3>
	<table class="props" cellpadding="0" cellspacing="0">
		<tr>
			<th>属性名</th>
			<th>类型</th>
			<th>默认值</th>
			<th>描述</th>
		</tr>
		<tr>
			<td>label</td>
			<td>String</td>
			<td>null</td>
			<td>选项名</td>
		</tr>
		<tr>
			<td>value</td>
			<td>String</td>
			<td>null</td>
			<td>选项值</td>
		</tr>
		<tr>
			<td>checked</td>
			<td>Boolean</td>
			<td>false</td>
			<td>选中状态</td>
		</tr>
		<tr>
			<td>disabled</td>
			<td>Boolean</td>
			<td>false</td>
			<td>禁止选择</td>
		</tr>
	</table>
  </div>
</template>

<script>
export default {
  name: 'checkbox',
  data () {
    return {
      	
    }
  },
  methods:{
	
  }
}
</script>
<style scoped>

</style>



